
<template>
  <div id="page-content" class="header-static">
    <div class="container" v-show="this.currentTestTime < this.maxTestTime">
      <div class="row padding-onlytop-md" v-show="options.title">
        <div class="col-md-12 center">
          <div>
            <h1 class v-text="options.title"></h1>
          </div>
        </div>
      </div>
      <div class="row padding-onlytop-md">
        <div class="col-md-12 center">
          <div>
            <span class="largeCharacter bold" v-text="currentKana[kanaForms[0]]"></span>
          </div>
        </div>
      </div>
      <div class="row padding-onlytop-sm center">
        <div
          class="candidateWrapper bold"
          v-bind:key="index"
          v-for="(item,index) in firstCandidates"
        >
          <div
            class="candidate"
            v-bind:class="{'gray':item.status=='gray','green':item.status=='green','red':item.status=='red','bigGreen':item.status=='bigGreen'}"
            @click="confirm($event,item,1)"
          >{{item[kanaForms[1]]}}</div>
        </div>
      </div>
      <div class="row padding-onlybottom-lg center">
        <div class="candidateWrapper" v-bind:key="index" v-for="(item,index) in secondCandidates">
          <div
            class="candidate"
            v-bind:class="{'gray':item.status=='gray','green':item.status=='green','red':item.status=='red','bigGreen':item.status=='bigGreen'}"
            @click="confirm($event,item,2)"
          >{{item[kanaForms[2]]}}</div>
        </div>
      </div>
    </div>
    <div class="container" v-show="this.currentTestTime >= this.maxTestTime">
      <div class="row padding-onlytop-lg">
        <div class="col-md-12 center">
          <div class="testComplete">测试完成，正确率</div>
          <div
            class="testRate bold padding-onlytop-lg"
            v-bind:class="testResultClass"
          >{{(correctAnswer *100 / totalAnswer).toFixed(2)}}%</div>
          <div class="tryAgain" v-bind:class="tryAgainBtnClass" @click="tryAgain">再来一遍</div>
        </div>
      </div>
    </div>
  </div>
</template>
 
<script>
export default {
  /**组件的属性 */
  props: {
    options: {
      type: Object,
      //这样设置默认值
      default: {
        title: '',
        maxTestTime: '',
      },
    },
  },
  mounted: function () {
    this.showNext()
  },
  watch: {},
  computed: {
    testResultClass: function () {
      var currectRate = this.correctAnswer / this.totalAnswer
      if (currectRate < 0.3) {
        return 'red-text'
      } else if (currectRate < 0.6) {
        return 'yellow-text'
      }
      return 'green-text'
    },
    tryAgainBtnClass: function () {
      var currectRate = this.correctAnswer / this.totalAnswer
      if (currectRate < 0.3) {
        return 'red'
      } else if (currectRate < 0.6) {
        return 'yellow'
      }
      return 'green'
    },
  },
  methods: {
    tryAgain: function () {
      this.totalAnswer = 0
      this.correctAnswer = 0
      this.currentTestTime = 0
    },
    /**
     * 换下一个假名
     */
    showNext: function () {
      //随机挑选一个作为题目
      this.currentKana = this.randomPick(this.kana)
      this.largeCharacter = this.currentKana.hiragana
      this.randomSort(this.kanaForms)
      //生成候选项
      this.generateCandidates()
      this.confirmedRowNumber = []
    },
    /**
     * 点击确定
     */
    confirm: function (event, item, rowNumber) {
      var candicates =
        rowNumber == 1 ? this.firstCandidates : this.secondCandidates
      //本行没有被选择
      if (this.confirmedRowNumber.indexOf(rowNumber) < 0) {
        var currentCorrect = false
        var currentHiragana = this.currentKana.hiragana
        if (item.hiragana == currentHiragana) {
          item.status = 'green'
          this.totalAnswer++
          this.correctAnswer++
          currentCorrect = true
        } else {
          item.status = 'red'
          //需要把对的标出来
          var rightKana = candicates.filter(function (candidate) {
            return candidate.hiragana == currentHiragana
          })[0]
          rightKana.status = 'bigGreen'
          this.totalAnswer++
        }
        //已选一行
        if (this.confirmedRowNumber.length == 1) {
          //本次是正确的
          if (currentCorrect) {
            setTimeout(this.showNext, 400)
          } else {
            //本次错误，高亮正确的值
            setTimeout(this.showNext, 2000)
          }
          //已完成一轮选择
          this.currentTestTime++
          var firstCorrect =
            this.firstCandidates.filter(function (item) {
              return item.status == 'red'
            }).length < 1
          var fsecondCorrect =
            this.secondCandidates.filter(function (item) {
              return item.status == 'red'
            }).length < 1
          if (firstCorrect && fsecondCorrect) {
            //本轮选择全对，onSuccess回调
            this.$emit('onSuccess')
          }
        }
        this.confirmedRowNumber.push(rowNumber)
      }
    },
    /**
     * 从array数组中随机挑选1个元素
     */
    randomPick: function (array) {
      return array[Math.floor(Math.random() * array.length)]
    },
    /**
     *  从array数组中随机不重复的挑选amount个元素
     */
    randomPickIsolate: function (array, amount) {
      //先把数组随机排列，然后再返回前amount个元素
      this.randomSort(array)
      return array.slice(0, amount)
    },
    //生成候选项
    generateCandidates: function () {
      this.firstCandidates = []
      this.secondCandidates = []
      //拷贝kana数组，删除正确的那一个，然后随机选取
      //减一是因为还要加入一个正确的
      var currentKana = this.currentKana
      this.firstCandidates = this.randomPickIsolate(
        this.deepCopy(this.kana).filter(function (item) {
          return item.hiragana != currentKana.hiragana
        }),
        this.candidatesAmount - 1
      )
      this.secondCandidates = this.randomPickIsolate(
        this.deepCopy(this.kana).filter(function (item) {
          return item.hiragana != currentKana.hiragana
        }),
        this.candidatesAmount - 1
      )
      this.firstCandidates.push(this.deepCopy(this.currentKana))
      this.secondCandidates.push(this.deepCopy(this.currentKana))
      //再随机排列
      this.randomSort(this.firstCandidates)
      this.randomSort(this.secondCandidates)
    },
    randomSort: function (array) {
      array.sort(function () {
        return 0.5 - Math.random()
      })
    },
    deepCopy: function (obj) {
      // 判断是否是对象
      if (typeof obj !== 'object') return
      // 判断obj类型，根据类型新建一个对象或者数组
      var newObj = obj instanceof Array ? [] : {}
      // 遍历对象，进行赋值
      for (var key in obj) {
        if (obj.hasOwnProperty(key)) {
          let val = obj[key]
          // 判断属性值的类型，如果是对象，递归调用deepCopy
          newObj[key] = typeof val === 'object' ? this.deepCopy(val) : val
        }
      }
      return newObj
    },
  },
  data: function () {
    return {
      //总点击次数
      totalAnswer: 0,
      //正确的点击次数
      correctAnswer: 0,
      //共测试这么多个假名，先取外部传过来的
      maxTestTime: this.options.maxTestTime || 10,
      //当前已测试这么多假名
      currentTestTime: 0,
      //第几行已确认选择
      confirmedRowNumber: [],
      //当前假名
      currentKana: {},
      //第一列候选项
      firstCandidates: [],
      //第二列候选项
      secondCandidates: [],
      //候选值个数
      candidatesAmount: 5,
      //假名形式
      kanaForms: ['hiragana', 'katakana', 'romaji'],
      kana: [
        { hiragana: 'あ', katakana: 'ア', romaji: 'a', status: 'gray' },
        { hiragana: 'い', katakana: 'イ', romaji: 'i', status: 'gray' },
        { hiragana: 'う', katakana: 'ウ', romaji: 'u', status: 'gray' },
        { hiragana: 'え', katakana: 'エ', romaji: 'e', status: 'gray' },
        { hiragana: 'お', katakana: 'オ', romaji: 'o', status: 'gray' },
        { hiragana: 'か', katakana: 'カ', romaji: 'ka', status: 'gray' },
        { hiragana: 'き', katakana: 'キ', romaji: 'ki', status: 'gray' },
        { hiragana: 'く', katakana: 'ク', romaji: 'ku', status: 'gray' },
        { hiragana: 'け', katakana: 'ケ', romaji: 'ke', status: 'gray' },
        { hiragana: 'こ', katakana: 'コ', romaji: 'ko', status: 'gray' },
        { hiragana: 'さ', katakana: 'サ', romaji: 'sa', status: 'gray' },
        { hiragana: 'し', katakana: 'シ', romaji: 'shi', status: 'gray' },
        { hiragana: 'す', katakana: 'ス', romaji: 'su', status: 'gray' },
        { hiragana: 'せ', katakana: 'セ', romaji: 'se', status: 'gray' },
        { hiragana: 'そ', katakana: 'ソ', romaji: 'so', status: 'gray' },
        { hiragana: 'た', katakana: 'タ', romaji: 'ta', status: 'gray' },
        { hiragana: 'ち', katakana: 'チ', romaji: 'chi', status: 'gray' },
        { hiragana: 'つ', katakana: 'ツ', romaji: 'tsu', status: 'gray' },
        { hiragana: 'て', katakana: 'テ', romaji: 'te', status: 'gray' },
        { hiragana: 'と', katakana: 'ト', romaji: 'to', status: 'gray' },
        { hiragana: 'な', katakana: 'ナ', romaji: 'na', status: 'gray' },
        { hiragana: 'に', katakana: 'ニ', romaji: 'ni', status: 'gray' },
        { hiragana: 'ぬ', katakana: 'ヌ', romaji: 'nu', status: 'gray' },
        { hiragana: 'ね', katakana: 'ネ', romaji: 'ne', status: 'gray' },
        { hiragana: 'の', katakana: 'ノ', romaji: 'no', status: 'gray' },
        { hiragana: 'は', katakana: 'ハ', romaji: 'ha', status: 'gray' },
        { hiragana: 'ひ', katakana: 'ヒ', romaji: 'hi', status: 'gray' },
        { hiragana: 'ふ', katakana: 'フ', romaji: 'fu', status: 'gray' },
        { hiragana: 'へ', katakana: 'ヘ', romaji: 'he', status: 'gray' },
        { hiragana: 'ほ', katakana: 'ホ', romaji: 'ho', status: 'gray' },
        { hiragana: 'ま', katakana: 'マ', romaji: 'ma', status: 'gray' },
        { hiragana: 'み', katakana: 'ミ', romaji: 'mi', status: 'gray' },
        { hiragana: 'む', katakana: 'ム', romaji: 'mu', status: 'gray' },
        { hiragana: 'め', katakana: 'メ', romaji: 'me', status: 'gray' },
        { hiragana: 'も', katakana: 'モ', romaji: 'mo', status: 'gray' },
        { hiragana: 'や', katakana: 'ヤ', romaji: 'ya', status: 'gray' },
        { hiragana: 'ゆ', katakana: 'ユ', romaji: 'yu', status: 'gray' },
        { hiragana: 'よ', katakana: 'ヨ', romaji: 'yo', status: 'gray' },
        { hiragana: 'ら', katakana: 'ラ', romaji: 'ra', status: 'gray' },
        { hiragana: 'り', katakana: 'リ', romaji: 'ri', status: 'gray' },
        { hiragana: 'る', katakana: 'ル', romaji: 'ru', status: 'gray' },
        { hiragana: 'れ', katakana: 'レ', romaji: 're', status: 'gray' },
        { hiragana: 'ろ', katakana: 'ロ', romaji: 'ro', status: 'gray' },
        { hiragana: 'わ', katakana: 'ワ', romaji: 'wa', status: 'gray' },
        { hiragana: 'を', katakana: 'ヲ', romaji: 'wo', status: 'gray' },
        { hiragana: 'が', katakana: 'ガ', romaji: 'ga', status: 'gray' },
        { hiragana: 'ぎ', katakana: 'ギ', romaji: 'gi', status: 'gray' },
        { hiragana: 'ぐ', katakana: 'グ', romaji: 'gu', status: 'gray' },
        { hiragana: 'げ', katakana: 'ゲ', romaji: 'ge', status: 'gray' },
        { hiragana: 'ご', katakana: 'ゴ', romaji: 'go', status: 'gray' },
        { hiragana: 'ざ', katakana: 'ザ', romaji: 'za', status: 'gray' },
        { hiragana: 'じ', katakana: 'ジ', romaji: 'ji', status: 'gray' },
        { hiragana: 'ず', katakana: 'ズ', romaji: 'zu', status: 'gray' },
        { hiragana: 'ぜ', katakana: 'ゼ', romaji: 'ze', status: 'gray' },
        { hiragana: 'ぞ', katakana: 'ゾ', romaji: 'zo', status: 'gray' },
        { hiragana: 'だ', katakana: 'ダ', romaji: 'da', status: 'gray' },
        { hiragana: 'ぢ', katakana: 'ヂ', romaji: 'ji', status: 'gray' },
        { hiragana: 'づ', katakana: 'ヅ', romaji: 'zu', status: 'gray' },
        { hiragana: 'で', katakana: 'デ', romaji: 'de', status: 'gray' },
        { hiragana: 'ど', katakana: 'ド', romaji: 'do', status: 'gray' },
        { hiragana: 'ば', katakana: 'バ', romaji: 'ba', status: 'gray' },
        { hiragana: 'び', katakana: 'ビ', romaji: 'bi', status: 'gray' },
        { hiragana: 'ぶ', katakana: 'ブ', romaji: 'bu', status: 'gray' },
        { hiragana: 'べ', katakana: 'ベ', romaji: 'be', status: 'gray' },
        { hiragana: 'ぼ', katakana: 'ボ', romaji: 'bo', status: 'gray' },
        { hiragana: 'ぱ', katakana: 'パ', romaji: 'pa', status: 'gray' },
        { hiragana: 'ぴ', katakana: 'ﾋﾟ', romaji: 'pi', status: 'gray' },
        { hiragana: 'ぷ', katakana: 'ﾌﾟ', romaji: 'pu', status: 'gray' },
        { hiragana: 'ぺ', katakana: 'ペ', romaji: 'pe', status: 'gray' },
        { hiragana: 'ぽ', katakana: 'ポ', romaji: 'po', status: 'gray' }
      ],
    }
  },
}
</script>
<style>
/*小屏优先，小屏的样式需要放在大屏的前面*/
.largeCharacter {
  font-size: 100px;
  color: var(--main-color);
  text-shadow: 8px 8px 17px rgba(0, 0, 0, 0.3);
  user-select: none;
}

.candidateWrapper {
  width: 57px;
  height: 93px;
  display: inline-block;
  user-select: none;
}

.candidate {
  font-size: 25px;
  border-radius: 12px;
  color: white;
  width: 52px;
  height: 55px;
  line-height: 53px;
  cursor: pointer;
  display: inline-block;
  /*这三个实现垂直居中动画过度从中心开始*/
  transform: translate(0, -50%);
  top: 50%;
  position: relative;
  /*动画缓动*/
  transition-duration: 0.2s;
}

.candidate.gray:hover {
  background-color: var(--main-color);
  color: white;
  box-shadow: 8px 8px 17px rgba(0, 0, 0, 0.3);
  width: 54px;
  height: 58px;
}

.tryAgain {
  border-radius: 9px;
  width: 179px;
  height: 70px;
  line-height: 70px;
  color: white;
  display: inline-block;
  margin-top: 57px;
  font-size: 31px;
  user-select: none;
  cursor: pointer;
  transition-duration: 0.2s;
  margin-bottom: 80px;
}

.tryAgain:hover {
  box-shadow: 8px 8px 17px rgba(0, 0, 0, 0.3);
}

.testComplete {
  font-size: 32px;
  color: var(--main-color);
  text-shadow: 8px 8px 17px rgba(0, 0, 0, 0.3);
  user-select: none;
  font-weight: bold;
}

.testRate {
  font-size: 67px;
  text-shadow: 8px 8px 17px rgba(0, 0, 0, 0.3);
}

/*大屏下的样式，只写不同的地方*/
@media (min-width: 768px) {
  .largeCharacter {
    font-size: 160px;
  }

  .candidateWrapper {
    width: 100px;
    height: 125px;
    margin: 29px 25px 0px 31px;
  }

  .candidate {
    font-size: 70px;
    width: 120px;
    height: 120px;
    line-height: 120px;
  }

  .candidate.gray:hover {
    width: 125px;
    height: 125px;
  }

  .testComplete {
    font-size: 74px;
  }

  .testRate {
    font-size: 130px;
  }

  .tryAgain {
    width: 259px;
    height: 83px;
    line-height: 83px;
    margin-top: 80px;
    font-size: 45px;
    margin-bottom: 80px;
  }
}

.bold {
  font-weight: bold;
}

.gray {
  background-color: var(--background-gray);
}

.green {
  background-color: #8bc97c;
}

.bigGreen {
  background-color: #8bc97c;
  animation: scaleAndResume 0.5s;
}

@keyframes scaleAndResume {
  0% {
    transform: translate(0, -50%) scale(1);
  }
  50% {
    transform: translate(0, -50%) scale(1.1);
  }

  100% {
    transform: translate(0, -50%) scale(1);
  }
}

.yellow {
  background-color: #ffcd18;
}

.red {
  background-color: #ff4949;
}

.gray-text {
  color: #d1d1d1;
}

.green-text {
  color: #8bc97c;
}

.yellow-text {
  color: #ffcd18;
}

.red-text {
  color: #ff4949;
}
</style>

